/**
 * Estilização do app
 */
.reset-button,
.floater-site .closebutton {
  -webkit-appearance: none;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  background: 0 0;
  border: 0;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
* {
  padding: 0;
}
@font-face {
  font-family: 'vipservice';
  src: url("../fonts/vipservice.eot");
  src: url("../fonts/vipservice.eot#iefix") format('embedded-opentype'), url("../fonts/vipservice.woff") format('woff'), url("../fonts/vipservice.ttf") format('truetype'), url("../fonts/vipservice.svg#vipservice") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "vipservice";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  font-size: 120%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(127,127,127,0.3);
}
.floater-background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background-color: rgba(0,0,0,0.8);
  opacity: 0;
  visibility: hidden;
  transition: 0.15s linear opacity;
}
.floater-background.js-active {
  opacity: 1;
  visibility: visible;
}
.floater-gallery .floater-gallery-icon {
  position: absolute;
  top: calc(50% - 20px);
  color: #fff;
  font-size: 60px;
  transition: 0.2s all linear;
  transform-origin: center;
  z-index: 1;
}
.floater-gallery .floater-gallery-icon:hover,
.floater-gallery .floater-gallery-icon:focus,
.floater-gallery .floater-gallery-icon:active {
  transform: scale(1.2);
}
.floater-gallery .floater-gallery-icon.-left {
  left: -100px;
}
.floater-gallery .floater-gallery-icon.-right {
  right: -100px;
}
.floater-gallery .floater-gallery-icon.js-hidden {
  display: none;
}
.floater-gallery .floater-gallery-icon .icon:before {
  text-shadow: 1px 1px 3px #000;
}
.floater-gallery .floater-gallery-message {
  color: #fff;
}
.floater-gallery .floater-gallery-message.js-hidden {
  display: none;
}
.floater-gallery .floater-gallery-image.-loader.js-hidden {
  display: none;
}
.floater-gallery .floater-gallery-image.-photo {
  transform: scale(1);
  transform-origin: center;
  visibility: visible;
  transition: 0.2s all linear;
}
.floater-gallery .floater-gallery-image.-photo.js-hidden {
  visibility: hidden;
  transform: scale(0);
  transition: none;
}
@media (max-width: $screen-sm) {
  .floater-gallery .floater-gallery-icon.-left {
    left: 30px;
  }
  .floater-gallery .floater-gallery-icon.-right {
    right: 30px;
  }
}
.floater-site {
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  opacity: 0;
  transition: all 0.15s linear;
  visibility: hidden;
  overflow: hidden;
  position: fixed;
  z-index: 9999999;
}
.floater-site.js-active {
  opacity: 1;
  visibility: visible;
}
.floater-site.js-active .floater-site-dialog {
  transform: scale(1);
}
.floater-site.-gallery .floater-site-content img {
  height: auto;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.floater-site.-medium .floater-site-dialog {
  max-width: 600px;
}
.floater-site.-large .floater-site-dialog {
  max-width: 900px;
}
.floater-site.-bg .floater-site-dialog .floater-site-content {
  background-color: #fff;
  background-clip: padding-box;
}
.floater-site .floater-site-dialog {
  margin: 50px auto;
  transform: scale(1.5);
  transform-origin: center;
  transition: 0.3s ease-in-out all;
  position: relative;
}
.floater-site .floater-site-dialog .floater-site-content {
  position: relative;
  outline: 0;
}
.floater-site .close {
  position: absolute;
  right: 10px;
  top: -40px;
  font-size: 35px;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-shadow: 0 1px 0 #fff;
  transition: 0.2s all ease-in-out;
  transform-origin: center;
}
.floater-site .close:hover,
.floater-site .close:focus,
.floater-site .close:active {
  transform: scale(1.1);
}
